<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-calendar"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" label-width="230px">
          <el-form-item label="(MB)">
            <el-input v-model="form.ext_max_size"></el-input>
          </el-form-item>
          <el-form-item label="()">
            <el-input v-model="form.ext_type"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.ext_isopen_watermark"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.sms_watermark_url"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="form.sms_watermark_position">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import qs from "Qs";
export default {
  name: "baseform",
  data() {
    return {
      form: {}
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.$api.config.get({}).then(res => {
        this.form = res.data;
      });
    },
    onSubmit() {
      var data = [];
      for (let item in this.form) {
        data[`config[${item}]`] = this.form[item];
      }
      console.log(data);
      this.$api.config.set(qs.stringify(data)).then(res => {
        console.log(res.data);
      });
      this.$message.success("");
    }
  }
};
</script>


